
import { defineComponent } from 'vue';
import { v4 as uuid } from 'uuid';
import appStorage from '@/store';
import { LineConfig, TableColumn } from '@/types';

export default defineComponent({
  name: 'ConfigLines',
  setup() {
    const tableColumns: TableColumn<LineConfig>[] = [
      { 
        name: "name",
        label: 'Name',
        align: 'left',
        field: (row: LineConfig) => row.name,
      },
      { 
        name: "numberOfWasp",
        label: 'WASP',
        align: 'left',
        field: (row: LineConfig) => row.numberOfWasp,
      },
      { 
        name: "numberOfO2Incubator",
        label: 'Air incubator',
        align: 'left',
        field: (row: LineConfig) => row.numberOfO2Incubator,
      },
      { 
        name: "numberOfCO2Incubator",
        label: 'CO2 incubator',
        align: 'left',
        field: (row: LineConfig) => row.numberOfCO2Incubator,
      },
      { 
        name: "collaborativeStation",
        label: 'Collaborative station',
        align: 'left',
        field: (row: LineConfig) => row.collaborativeStation,
      },
      { 
        name: "radian",
        label: 'Radian',
        align: 'left',
        field: (row: LineConfig) => row.radian,
      },
      { 
        name: "phenomatrix",
        label: 'Phenomatrix',
        align: 'left',
        field: (row: LineConfig) => row.phenomatrix,
      },
      { 
        name: "waspOccupancyRate",
        label: 'WASP Occupancy rate', //TODO: tempo in % dei protocolli che sono su questa linea
        align: 'left',
        field: (row: LineConfig) => row.waspOccupancyRate,
      },
      { 
        name: "wasplabOccupancyRate",
        label: 'WASPLAB Occupancy rate', //TODO: tempo in % dei protocolli che sono su questa linea
        align: 'left',
        field: (row: LineConfig) => row.wasplabOccupancyRate,
      },
      { 
        name: "actions",
        label: '',
        align: 'left',
        field: (row: LineConfig) =>row.id,
      },
    ];

    const {
      lines,
      weightedDayTimesPerProtocol,
      dailyData
    } = appStorage();

    const peakDay = dailyData.value.find((x) => x.isPeakDay);

    return {
      tableColumns,
      lines,
      weightedDayTimesPerProtocol,
      peakDay
    }
  },
  watch: {
    lines: {
      handler: function () {
        this.lines.forEach(line => {
          let totalWaspTime = 0;
          let totalWasplabTime = 0;
          const protocols = line.protocols;
          protocols.forEach(protocol => {
            const waspProtocolTime = this.weightedDayTimesPerProtocol.filter((x) => x.protocol == protocol.id && x.dayOfWeek == this.peakDay?.dayOfWeek && (x.type == "plates" || x.type == "slides" || x.type == "broths")).map((x) => x.timeInSeconds).reduce((a, b) => a + b) * protocol.samples / 100;
            totalWaspTime += waspProtocolTime;

            const wasplabProtocolTime = this.weightedDayTimesPerProtocol.filter((x) => x.protocol == protocol.id && x.dayOfWeek == this.peakDay?.dayOfWeek && !(x.type == "plates" || x.type == "slides" || x.type == "broths")).map((x) => x.timeInSeconds).reduce((a, b) => a + b) * protocol.samples / 100;
            totalWasplabTime += wasplabProtocolTime;
          });
          line.waspOccupancyRate = totalWaspTime / 3600 / 24 * 100;
          line.wasplabOccupancyRate = totalWasplabTime / 3600 / 24 * 100;
        });
      },
      deep: true
    }
  },
  methods: {
    addLine() {
      this.lines.push({
        id: uuid(),
        collaborativeStation: false,
        name: "wasplab " + (this.lines.length + 1),
        numberOfCO2Incubator: 0,
        numberOfO2Incubator: 0,
        numberOfWasp: 0,
        phenomatrix: false,
        protocols: [],
        radian: false,
        waspOccupancyRate: 0,
        wasplabOccupancyRate: 0,
      });
    },
    removeLine(id: string) {
      const indexToRemove = this.lines.findIndex((x) => x.id == id);
      if(indexToRemove > -1) {
        this.lines.splice(indexToRemove, 1);
      }
    }
  }
});
